import { lazy, Suspense } from "react";
import { BrowserRouter, HashRouter, Route, Routes } from "react-router-dom";

const CompaniesPage = lazy(() => import("./companies"));
const CompaniesDetailsPage = lazy(() => import("./companyDetails"));
const MeetingDetailsPage = lazy(() => import("./meetingDetails"));
const DocumentsTemplatePage = lazy(() => import("./documentEditing"));

const Routing = () => {
  return (
    <HashRouter>
      {/* <BrowserRouter> */}
        <Suspense fallback={<div>...</div>}>
          <Routes>
            <Route path="" element={<CompaniesPage />} />
            <Route path="/companies/" element={<CompaniesPage />} />
            <Route path="/companies/:id" element={<CompaniesDetailsPage />} />
            <Route path="/meetings/:id" element={<MeetingDetailsPage />} />
            <Route path="/templates/:type" element={<DocumentsTemplatePage />} />
          </Routes>
        </Suspense>
      {/* </BrowserRouter> */}
    </HashRouter>
  );
};

export default Routing;
